function getRandom() {
  const v = Math.ceil(Math.random() * 255).toString(16);
  return v.length < 2 ? '0' + v : v;
}

export function groupColor(groupPointList) {
  const groupHash: any = {};
  groupPointList.map(h => {
    if (!groupHash[h.no]) {
      groupHash[h.no] = `#${getRandom()}${getRandom()}${getRandom()}`;
    }
  });

  groupPointList.map(h => {
    h.color = groupHash[h.no];
  });
}
